.product-page__top {
  padding: 15px 0 0 0;
  display: flex;
  flex-wrap: wrap;
  color: black;
}

.product-page__top-left {
  width: 420px;
  height: 420px;
  text-align: center;
}
.product-page__top-left img {
  width: 400px;
  height: 400px;
  object-fit: contain;
}
.product-page__top-right {
  flex: 1 1 50%;
  display: flex;
  flex-direction: column;
}
.product-page__top-title {
  text-align: center;
  font-size: 1.5rem;
  min-height: 100px;
}
.product-page__top-buy-box {
  padding: 10px 20px;
  display: flex;
  flex-direction: column;
}
.product-page-price {
  height: 150px;
  color: #181818;
  font-weight: 700;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
}
.price-box__full-price {
  font-size: 2rem;
}
.price-box__credit-price {
  padding: 10px 0;
  font-size: 1.2rem;
}
.product-page__rating-category {
  display: flex;
  justify-content: space-between;
}

.product-page__rating img {
  width: 200px;
}
.product-page__category {
  font-weight: 500;
  color: rgba(0, 0, 0, 0.5);
  font-size: 1.2rem;
}
.product-page__bottom {
  padding: 10px 15px 0 15px;
  font-size: 1.3rem;
  color: black;
}

.page-already-in-basket button {
  background: rgb(255, 255, 255);
  color: #181818;
  cursor: auto;
}

@media (max-width: 992px) {
  .product-page__top {
    flex-direction: column;
    text-align: center;
    align-items: center;

    .product-page__top-left {
      max-width: 300px;
      max-height: 300px;
      img {
        height: auto;
        max-width: 100%;

        max-height: 100%;
      }
      margin-bottom: 15px;
    }

    .product-page__top-right {
      .product-page__top-title {
        width: 100%;
        min-height: auto;
      }
    }
    .product-page-price {
      flex-direction: column;
      height: 180px;
      button {
        font-size: 1.3rem;
        padding: 15px 15px;
      }
    }
    .product-page__rating-category {
      flex-direction: column;
    }
    .product-page__rating {
      margin-bottom: 10px;
    }
  }
}
