@import url(./productPage.scss);

main {
  flex: 1 1 auto;
  margin-bottom: 40px;
}
.error-message {
  width: 100vw;
  height: 100vh;
  background: black;
  color: white;
  display: flex;
  justify-content: center;
  align-items: center;
}
.main__box {
  display: flex;
  gap: 20px;
  margin-top: 120px;
}
.filter-box {
  min-width: 200px;
  text-align: center;
  min-height: 600px;
  background: url("../../public/images/sidefon.png");
  padding: 10px 10px;
  border-radius: 5px;
}
.filter__sort-ul {
  user-select: none;
  position: relative;
}
.view-sorted-lists-category {
  visibility: hidden;
}
.filter__sort-li {
  padding: 5px 10px;
}
.sorted__lists {
  display: flex;
  flex-direction: column;
  gap: 15px;
  li {
    cursor: pointer;
    position: relative;
    min-width: 170px;
    .change-select-li {
      color: rgb(0, 0, 0);
      &:hover {
        cursor: pointer;
      }
      display: inline-flex;
      justify-content: center;
      align-items: center;
      position: absolute;
      font-size: 1.1rem;
      font-weight: 900;
      padding-bottom: 3px;
      width: 20px;
      height: 20px;
      right: 10px;
      top: 0;
      background: rgb(255, 255, 255);
    }
  }
  z-index: 2;
  text-align: start;
  position: absolute;
  left: 0;
  top: 51px;
  padding: 10px 5px 10px 5px;
  border-bottom: 2px solid white;
}

.sorted__lists-category {
  position: absolute;
  display: flex;
  flex-direction: column;
  gap: 15px;
  top: 200px;
  li {
    cursor: pointer;
    padding: 10px;
    &:hover {
      border-left: 2px solid white;
      border-right: 2px solid white;
    }
  }
  li.category-li-active {
    background: white;
    color: #181818;
  }
}

.main__content {
  display: flex;
  flex-wrap: wrap;
  gap: 15px;
}
.product-box {
  overflow: hidden;
  z-index: 1;
  border-radius: 25px;
  box-shadow: 0 1px 1px rgba(0, 0, 0, 0.5);
  text-align: center;

  color: black;
  font-weight: 700;
  height: 300px;
  flex: 0 1 32%;
  margin-bottom: 20px;
  position: relative;

  .product__price {
    display: flex;
    justify-content: center;
    align-items: center;
    color: rgb(139, 0, 200);
    font-size: 2rem;
    height: 150px;
    max-width: calc(100% - 175px);
  }
  .product__category {
    position: absolute;
    bottom: 0;
    width: 100%;
    max-width: calc(100% - 195px);
    text-align: right;
    font-weight: 500;
    color: rgba(0, 0, 0, 0.5);
  }
  .product__title {
    cursor: pointer;
    height: 90px;
    padding: 5px 10px 0px 10px;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .product__img {
    cursor: pointer;
    position: absolute;
    right: -25px;
    bottom: -25px;
    width: 200px;
    height: 200px;
    img {
      object-fit: contain;
      width: 200px;
      height: 200px;
    }
  }
  &:hover {
    box-shadow: 0 0 10px rgb(168, 168, 168);
    .product__to-basket-box button,
    .already-in-basket button {
      visibility: visible;
    }
  }
  .product__to-basket-box {
    visibility: hidden;
    display: flex;
    align-items: center;
    justify-content: flex-end;
    position: absolute;
    left: 0;
    bottom: 32px;
    width: 45%;
    height: 43px;
  }
  .already-in-basket {
    display: flex;
    align-items: center;
    justify-content: flex-end;
    position: absolute;
    left: 0;
    bottom: 32px;
    width: 45%;
    height: 43px;
    button {
      border-radius: 20px;
      background: rgb(255, 255, 255);
      color: #181818;
      box-shadow: 0 2px 5px rgb(0, 0, 0);
      font-size: 1.2rem;
      padding: 10px 15px;
      cursor: auto;
      position: relative;
      overflow: hidden;
    }
  }
}
.product__rating {
  text-align: left;
}
.product__rating img {
  max-width: 100%;
  max-height: 100%;
  object-fit: cover;
}
.product__rating-05 {
  background: linear-gradient(to right, #181818 10%, rgba(0, 0, 0, 0.5) 10%);
}
.product__rating-10 {
  background: linear-gradient(to right, #181818 20%, rgba(0, 0, 0, 0.5) 20%);
}
.product__rating-15 {
  background: linear-gradient(to right, #181818 30%, rgba(0, 0, 0, 0.5) 30%);
}
.product__rating-20 {
  background: linear-gradient(to right, #181818 40%, rgba(0, 0, 0, 0.5) 40%);
}
.product__rating-25 {
  background: linear-gradient(to right, #181818 50%, rgba(0, 0, 0, 0.5) 50%);
}
.product__rating-30 {
  background: linear-gradient(to right, #181818 60%, rgba(0, 0, 0, 0.5) 60%);
}
.product__rating-35 {
  background: linear-gradient(to right, #181818 70%, rgba(0, 0, 0, 0.5) 70%);
}
.product__rating-40 {
  background: linear-gradient(to right, #181818 80%, rgba(0, 0, 0, 0.5) 80%);
}
.product__rating-45 {
  background: linear-gradient(to right, #181818 90%, rgba(0, 0, 0, 0.5) 90%);
}
.product__rating-50 {
  background: linear-gradient(to right, #181818 100%, rgba(0, 0, 0, 0.5) 100%);
}

@media (max-width: 1200px) {
  .product-box {
    flex: 1 1 40%;
    .product__to-basket-box button,
    .already-in-basket button {
      visibility: visible;
    }

    .product__price {
      font-size: 2rem;
    }
  }
}
@media (max-width: 992px) {
  .filter__sort-ul div {
    flex-direction: column;
  }
  .product-box {
    overflow: hidden;
    height: 450px;
    flex: 1 1 100%;
    .product__img {
      overflow: visible;
      left: calc(50% - 100px);
    }
    .product__rating {
      margin-top: 5px;
      display: flex;
      justify-content: center;
    }
    .product__category {
      display: none;
    }
    .product__title {
      height: 100px;
      font-size: 1.1rem;
    }
    .product__price {
      width: 100%;
      max-width: 100%;
      height: 75px;
      font-size: 2rem;
    }
    .product__to-basket-box,
    .already-in-basket {
      left: 0;
      bottom: 195px;
      width: 100%;
      justify-content: center;
      height: 50px;
    }
  }
}

@media (max-width: 767px) {
  .main__box {
    flex-direction: column;
    .product-box {
      box-shadow: 0 0 10px black;
      .product__title {
        height: auto;
      }
      .product__to-basket-box,
      .already-in-basket {
        position: relative;
        top: 0;
      }
    }
  }
  .filter-box {
    min-height: 200px;
    .sorted__lists {
      top: 0;
      position: relative;
      margin-bottom: 10px;
    }
    .view-sorted-lists-category {
      padding: 5px;
      visibility: visible;
      border-left: 2px solid white;
      border-right: 2px solid white;
      margin-bottom: 10px;
    }

    .sorted__lists-category {
      position: relative;
      right: 0;
      top: 0;
      display: none;
    }
    .sorted-lists-category-visible {
      display: block;
    }
  }
}
