.basket__page {
  position: relative;
  min-width: 80%;
  margin: 0 auto;
  color: rgb(0, 0, 0);
  display: flex;
  gap: 20px;
}
.basket__products {
  order: 1;
  flex-grow: 1;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.basket__item {
  &:hover {
    box-shadow: 0 0 10px rgb(168, 168, 168);
  }
  .basket__item-data {
    margin-right: 55px;
    text-align: center;
    word-break: keep-all;
    flex: 1 1 auto;
    display: flex;
    flex-direction: column;
    gap: 15px;
  }
  box-shadow: 0 1px 1px rgba(0, 0, 0, 0.5);
  border-radius: 25px;
  padding: 5px 10px;
  display: flex;
  justify-content: space-between;
  position: relative;
  .basket__item-category {
    color: rgba(0, 0, 0, 0.5);
  }
  .basket__item-remove-btn {
    &:hover {
      text-shadow: 0 0 1px rgba(2, 2, 2, 0.425);
    }
    color: rgb(139, 0, 200);
    position: absolute;
    align-self: center;
    right: 15px;
    bottom: 10px;
  }
  .basket__item-price {
    font-size: 2rem;
    font-weight: 700;
    color: rgb(139, 0, 200);
  }
}

.basket__mark-box {
  border-radius: 18px;
  border: 1px solid rgb(0, 0, 0);
  color: rgb(255, 255, 255);
  padding: 10px 15px;
  min-height: 80px;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  background: url("../../public/images/sidefon.png");
}
.basket__item-mark-btn {
  background: rgb(255, 255, 255);
  cursor: pointer;
  font-size: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  right: 15px;
  top: 5px;
  width: 50px;
  height: 50px;
  border: 2px solid rgba(0, 0, 0, 0.438);
  border-radius: 50%;
  color: rgb(255, 255, 255);
}
.basket__item-mark-btn-active {
  user-select: none;
  background: url("../../public/images/sidefon.png");
}
.basket__mark-box-mark-remove,
.basket__mark-box-mark-all {
  margin-right: 60px;
  cursor: pointer;
}
.basket__mark-box-mark-all {
  position: relative;
  .basket__mark-box-mark-all-full {
    user-select: none;
    border-radius: 50%;
    border: 1px solid rgb(0, 0, 0);
    box-shadow: 0 0 2px rgb(0, 0, 0) rgb(0, 0, 0);
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 2rem;
    color: rgb(0, 0, 0);
    position: absolute;
    top: -15px;
    right: -60px;
    width: 50px;
    height: 50px;
    background: rgb(255, 255, 255);
  }
}
.basket__pay {
  border: 1px solid rgb(0, 0, 0);
  border-radius: 18px;
  min-height: 600px;
  min-width: 200px;
  padding-top: 15px;
  text-align: center;

  order: 2;
  background: url("../../public/images/sidefon.png");
}
.basket__pay-container {
  position: sticky;
  top: 150px;
  .basket__pay-price {
    color: rgb(255, 255, 255);
    font-size: 2rem;
  }

  button {
    margin-top: 10px;
    color: rgb(255, 255, 255);
    padding: 10px 15px;
    border-radius: 25px;
    box-shadow: 0 0 5px rgb(255, 255, 255);
  }
}
.basket__item-img {
  border-radius: 25px;
  width: 200px;
  height: 200px;
}
.basket__item-img img {
  width: 200px;
  height: 200px;
  object-fit: contain;
}
.basket__item-img,
.basket__item-title,
.basket__item-remove-btn {
  cursor: pointer;
}
.basket__empty {
  text-align: center;
  font-size: 2rem;
  color: rgb(0, 0, 0);
  margin: 0 auto;
}

@media (max-width: 992px) {
  .basket__item {
    flex-direction: column;
    align-items: center;
    gap: 5px;
    padding: 5px 10px 35px 5px;
    .basket__item-data {
      margin-right: 0;
    }
    .basket__item-remove-btn {
      align-self: center;
      right: calc(50% - 78px);
    }
  }
  .basket__page {
    .basket__item-headers {
      display: none;
    }
  }
}
@media (max-width: 767px) {
  .basket__page {
    .basket__products {
      margin: 0 0;
    }
    flex-direction: column;
    .basket__pay {
      padding: 10px;
      display: flex;
      justify-content: center;
      position: static;
      min-height: auto;
      height: 120px;
      width: auto;
      order: 0;
    }

    .basket__mark-box {
      position: relative;
      & > div {
        text-align: center;
        padding: 25px 10px 24px 10px;
        margin: 0;
        width: 100%;
        &:first-child {
          border-bottom: 2px solid rgb(255, 255, 255);
        }
      }
      .basket__mark-box-mark-all {
        position: static;
        margin-right: 30px;
      }
      .basket__mark-box-mark-all-full {
        right: 15px;
        top: 94px;
      }
      justify-content: center;
      align-items: center;
      flex-direction: column;
    }
  }
}
