@import url("./styles/null-style.css");
html,
body {
  height: 100%;
  width: 100%;
  font-size: 18px;
}

.myBtn {
  position: relative;
  overflow: hidden;
  &:after {
    background: #fff;
    content: "";
    height: 155px;
    left: -75px;
    opacity: 0.2;
    position: absolute;
    top: -50px;
    transform: rotate(35deg);
    transition: all 550ms cubic-bezier(0.19, 1, 0.22, 1);
    width: 50px;
    z-index: 1;
  }
  border-radius: 5px;
  box-shadow: 0 2px 2px rgb(0, 0, 0);
  font-size: 1.2rem;
  padding: 10px 15px;
  color: white;
  background: url("../public/images/sidefon.png");
  &:hover {
    &:after {
      left: 120%;
      transition: all 550ms cubic-bezier(0.19, 1, 0.22, 1);
    }
  }
}

html {
  font-family: Arial, Helvetica, sans-serif;
  color: white;
}
.App {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}
.modale-window {
  z-index: 10;
  background: rgba(0, 0, 0, 0.259);
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  min-width: 100vw;
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.error {
  font-size: 1.2rem;
  color: red;
}
*[class*="__container"] {
  padding: 0 15px;
  max-width: 1400px;
  margin: 0 auto;
}
@media (max-width: 1200px) {
  *[class*="__container"] {
    max-width: 900px;
  }
}
@media (max-width: 992px) {
  *[class*="__container"] {
    max-width: 750px;
  }
  .add-product__form {
    margin: 0 15px;
    gap: 20px;
    text-align: center;
    input {
      font-size: 1.4rem;
    }
    button {
      font-size: 1.6rem;
    }
  }
}
